/* -------------
Sidebar
----------------*/
.sidebar {
  position: fixed; 
  width: 9%;
  height: 100%;
  top: 0%;
  margin-top: 52px;
  left: 0px;
  overflow:hidden;
  background-color: #fff;
  -webkit-transform: translateZ(0);
  visibility: visible;
  -webkit-backface-visibility: hidden;
  
  header{
    background-color: #09f;
    width: 100%;
    display:block;
    padding: 0.75em	1em;
  }
  ::-webkit-scrollbar-thumb {
    background: #fff; 
  }
  ::-webkit-scrollbar-thumb:hover {
    background: #fff; 
  }
  ::-webkit-scrollbar {
    width: 0px;
  }
}
::-webkit-scrollbar {
  width: 2px;
}

/* -------------
Sidebar Nav
----------------*/
.sidebar-nav:hover{
  overflow: auto;
  overflow-x: hidden;
}
.sidebar-nav {
  position: fixed; 
  background-color: #fff;
  height: 90%;
  width: 100%;
  font-weight: 400; 
  font-size: 1.2em; 
  padding-bottom: 0;
  z-index: 9;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
 
  
  ul{
    list-style:none;
    display: block;
    padding: 0;
    margin: 0;
    
    li{
      margin-left: 3%;
      padding-left: 0;
      min-width: 13em;
      display:inline-block;
      width: 100%;
      
      a{
        color: #333;
        font-size: 0.75em;
        padding: 1.05em	1em;
        position: relative;
        display:block;
        
        &:hover{
          color: #444;
          background: rgba(80,80,80, 0.05); 
          transition:all 0.6s ease;
        }
      }
/* -------------
Sidebar: icons
----------------*/
      i{
        font-size: 1.8em;
        padding-right: 0.5em;
        width: 9em;
        display: inline;
        vertical-align:middle;
      }  
    }
  }

  /* ---------------
  active cls
  ------------------- */

  .active{
    background-color: #09f;
    transition:all 0.6s ease;
  }


/* -------------
Chev elements
----------------*/ 
  & > ul > li > a:after {
    content: '\f125';
    font-family: ionicons;
    font-size: 0.5em;
    width: 10px;
    color: #000;
    position: absolute;
    right: 0.75em;
    top: 45%;
  }
/* -------------
Nav-Flyout
----------------*/
  & .nav-flyout {
    position: absolute;
    background-color: #fff;
    z-index: 9;
    left: 2.3em;
    top: 0;
    height: 100vh;
    transform:translateX(100%);
    transition:all 0.5s ease;
    box-shadow: 0 4px 11px 1px rgba(0, 0, 0, 0.06), 0 1px 0px rgba(0, 0, 0, 0.08);
    overflow: scroll;
    border-left: 1px solid #e3e4e9;

    a:hover{
      background-color: rgba(255,255,255, 0.05);
      
    }
  }

 
}
 

