.tableLabelHeader{
    font-weight: 500;
    font-size: 0.75rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    flex: 1;
    text-align: left;
    overflow: hidden;
    margin: 0% 1%;
}
.tableItemDivHeader {
    width: 100%;
    padding: 5px 0 5px 5%;
    box-shadow: 0 4px 6px rgba(32,33,36,.28);
    /* border-bottom: 1px solid #e6e6e6; */
    font-size: 0.75rem;
    font-weight: 500;
    background: #34ace0;
    color: #fff;
}
.tableItemDiv {
    width: 100%;
    padding: 5px;
    cursor: pointer;
    /* box-shadow: 0 0px 5px rgba(32,33,36,.28); */
    font-size: 0.75rem;
    cursor: pointer;
}
.tableArrowIcons {
    cursor: pointer;
    color: #777;
    margin: 9px 5px !important;
}

.FilterTableDivBody {
    padding: 0 0 0 0px;
    height: 30vh;
}
.AccondianTable{
    box-shadow: 0 0px 5px rgba(32,33,36,.28) !important;
    border-bottom: 0px !important;
}

.tabelListDiv {
    background: #fff;
    /* box-shadow: 0 4px 6px rgba(32,33,36,.28); */
    display: flex;
    flex-direction: column;
    list-style-type: none;
    margin: 0;
    padding: 0;
    border: 0;
    padding-bottom: 4px;
    overflow: hidden;
}
.tableLabelValues {
    /* width: 200px; */
    /* max-width: 200px; */
    /* min-width: 150px; */
    font-size: 0.75rem;
    /* padding-right: 10px; */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    flex: 1 1;
    margin: 0% 1%;
}
.imgviewpage{
    height: 120px !important;
    padding: 5px !important;
    margin: 5px;
    /* box-shadow: 0 4px 6px rgba(32,33,36,.28) !important; */
    border: 1px solid #e4e4e4;
    border-radius: 5px;
    cursor: pointer;
}
.cusorPointer{
    cursor: pointer;
}
.lastImg{
    display: flex;
    align-items: center;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}
.imgtotCount{
    position: absolute;
    left: 36%;
    top: 39%;
    font-size: 1.06rem;
    font-family: sans-serif;
    font-weight: bold;
    color: white;
}

.Imgbgmstyle{
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}
.previewFile{
    color: rgb(119, 119, 119);
    width: 100% !important;
    height: 30% !important;
    margin-top: 1em;
}
.homeActive{
    color: #337ab7;
}