  /* Toggle button */
.toggle-button-cover
{
    display: table-cell;
    position: relative;
    width: 200px;
    height: 140px;
    box-sizing: border-box;
}

.button-cover
{
    height: 100px;
    margin: 20px;
    background-color: #fff;
    box-shadow: 0 10px 20px -8px #c5d6d6;
    border-radius: 4px;
}

.button-cover:before
{
    counter-increment: button-counter;
    content: counter(button-counter);
    position: absolute;
    right: 0;
    bottom: 0;
    color: #d7e3e3;
    font-size: 0.75rem;
    line-height: 1;
    padding: 5px;
}

.button-cover, .knobs, .layer
{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.button
{
    position: relative;
    top: 50%;
    width: 75px;
    height: 28px;
    margin: 0px auto 0 auto;
    overflow: hidden;
    border: 1px solid #ebecf1;
}

.button.r, .button.r .layer
{
    border-radius: 100px;
}

.button.b2
{
    border-radius: 2px;
}

.checkbox
{
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    opacity: 0;
    cursor: pointer;
    z-index: 3;
}

.knobs
{
    z-index: 2;
}

.layer
{
    width: 100%;
    background-color: #fff;
    transition: 0.3s ease all;
    z-index: 1;
}
#button-11
{
    overflow: visible;
}

#button-11 .knobs
{
    perspective: 70px;
}

#button-11 .knobs:before, #button-11 .knobs:after, #button-11 .knobs span
{
    position: absolute;
    top: 4px;
    border-radius: 2px;
}

#button-11 .knobs:before, #button-11 .knobs:after
{
    width: 30px;
    height: 20px;
    color: #444;
    font-size: 0.625rem;
    font-weight: bold;
    text-align: center;
    line-height: 1;
    padding: 5px 0px;
}

#button-11 .knobs:before
{
    content: 'List';
    left: 4px;
}

#button-11 .knobs:after
{
    content: 'Card';
    right: 4px;
}

#button-11 .knobs span
{
    right: 1px;
    width: 35px;
    height: 18px;
    background-color: #337ab7;
    transform: rotateY(0);
    transform-origin: 0% 50%;
    transition: 0.6s ease all;
    z-index: 1;
}

#button-11 .checkbox:checked + .knobs span
{
    transform: rotateY(-180deg);
    background-color: #337ab7;
}

#button-11 .checkbox:checked ~ .layer
{
    /* background-color: #fcebeb; */
}
